async function getCode(retry = 0) {
  if (sendCodeBtnStatus.value === SendCodeBtnStatus.COOLING) {
    return
  }
  //
  if (!mobile.value) {
    showToast(_T('请输入您的手机号'))
    return
  }
  if (!country.value) {
