  } catch (error) {
    reportError(error)
  }

  sendCodeBtnStatus.value = SendCodeBtnStatus.COOLING
  // 上报结果
  const [success, { result, data, message, toast }] = await getMobileCode(
    mobile.value,
    country.value
  )
  loading.value = false
  if (success && result === 0) {
    try {
      reportCodeResult(`${country.value}`, `${mobile.value}`, `${result}`, `${message || ''}`)
    } catch (error) {
      reportError(error)
    }
  } else if (result === -20000) {
